@import "global";
@import "reset";
@import "slick-carousel/slick/slick.css";

$desktop: 1600px;

@mixin breakpoint($point) {
  @if $point == desktop {
    @media (min-width: $desktop) {
      @content;
		}
	}
}

.header__wrapper {
  min-height: 100%;
}
.header-parallax {
  min-height: 100vh;
}
.header-parallax__body {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.header-parallax__text-container {
  display: flex;
  flex-direction: column;
  width: max-content;
  text-align: center;

  margin: 150px;
  font-size: 30px;
  z-index: 2;
  padding: 25px;
  position: relative;
  backdrop-filter: blur(4px);
  background-color: rgba(100, 100, 101, 0.37);
  border-radius: 5px;
  .header-parallax__label {
    display: flex;
    margin: 0 0 10px 0;
    align-items: center;
    white-space: nowrap;
  }
  .header-parallax__label::before {
    flex: 0 0 70px;
    content: "";
    margin: 0 30px 0 0;
    height: 1px;
    background-color: #7d9900;
  }
  .header-parallax__title {
    margin-top: 10px;
    font-size: 60px;
    span {
      font-weight: 900;
      text-transform: uppercase;
      color: #7d9900;
    }
  }
}
._container {
  max-width: $desktop;
  margin: 0px auto;
  padding: 50px 20px;
}

.header-parallax__images {
  position: absolute;
  width: 120%;
  height: 120%;
  top: -10%;
  left: -10%;
  z-index: 1;
}
.images-parallax {
}
.images-parallax__item {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.images-parallax_icons,
.images-parallax_icons-greyscale,
.images-parallax_humans {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}
.images-parallax_icons {
  background: url(../img/bgSiteBlackWhite.png) top/cover no-repeat;
  opacity: 25%;
}
.images-parallax_icons-greyscale {
  background: url(../img/bgSite.png) top/cover no-repeat;
  z-index: 2;
}
.images-parallax_humans {
  background: url(../img/photo_for_site_horizontalGreyscale.png) top/cover
    no-repeat;
  z-index: 3;
}

.navigation {
  font-size: 18px;
  position: fixed;
  top: 0px;
  margin: 2% auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  padding: 0;
  width: max-content;
  box-shadow: 0 8px 32px 0 rgba(100, 100, 101, 0.37);
  backdrop-filter: blur(4px);
  background-color: rgba(100, 100, 101, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 5px;
  z-index: 5;

  ul {
    display: flex;
    padding: 20px 100px;
    justify-content: space-around;
    gap: 100px;
  }

  ul a {
    position: relative;
  }
  ul a::after {
    content: "";
    width: 0;
    height: 3px;
    background: #7d9900;
    position: absolute;
    left: 0;
    bottom: -6px;
    transition: 0.2s ease-in-out;
  }
  ul a:hover::after {
    width: 100%;
  }
}

//   -------------------about---------------------------

.about {
  padding: 80px 0;
  position: relative;
  color: #ababab;
  background: #171717;
  height: 100%;
  z-index: 2;
  &::before {
    content: "";
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 150px;
    transform: translate(0px, -100%);
    background: linear-gradient(180deg, rgba(11, 29, 38, 0) 5%, #171717 100%);
  }
  &::after {
    content: "";
    pointer-events: none;
    position: absolute;
    bottom:0;
    left: 0;
    width: 100%;
    height: 150px;
    transform: translate(0px, 97%);
    background: linear-gradient(0deg, rgba(11, 29, 38, 0) 5%, #171717 100%);
  }
  .about-row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    z-index: 2;
    &__col-1 {
      flex-basis: 35%;
      img {
        width: 100%;
        border-radius: 15px;
      }
    }
    &__col-2 {
      flex-basis: 60%;
    }
    &__title {
      font-size: 40px;
      text-align: center;
      font-weight: 900;
    }
    &__tabs-nav {
      display: flex;
      gap: 30px;
      justify-content: flex-start;
    }
    &__tabs-link {
      margin-top: 10px;
      position: relative;
      font-size: 20px;
      font-weight: 500;
      cursor: pointer;
    }

    &__tabs-link::after {
      content: "";
      width: 0;
      height: 3px;
      background-color: #7d9900;
      position: absolute;
      left: 0;
      bottom: -8px;
      transition: 0.5s;
    }
    &__tabs-link.active-link::after {
      width: 50%;
    }
    &__tab-item {
      display: none;
    }
    &__tab-item.active-tab {
      padding-top: 20px;
      display: block;
    }
  }
}

//   -------------------skills---------------------------

.skills {
  padding: 180px 0;
  position: relative;
  color: #fff;
  background: #080808;
  height: 100%;
  z-index: 1; 
      &__title{
        font-size: 40px;
      text-align: center;
      font-weight: 900;
    }
    &__text-container{
      text-transform: capitalize;
        display: flex;
        margin: 0 auto;
        font-weight: 500;
        flex-direction: column;
        justify-content: center;
        align-items: center;
       gap: 100px;
        font-size: 40px;
        position: relative;
    }
  }
.text-label__hard-skills{
  display: flex;
  justify-content:flex-start;
  align-items: center;
  gap: 200px;
}
.skills__hard-carousel{
